import React, {Component} from 'react';
import {connect} from 'react-redux';
import JSZip from 'jszip';
import FileSaver from 'file-saver';

import {simulateClick, objKeysToArr, objKeyArray, getFileExtension, formatAudioObj} from '../Helpers'

import {addEffect, loadEffects} from '../actions/effectsActions';
import {loadSounds} from '../actions/soundsActions';

class ControlsContainer extends Component{
  constructor(props){
    super(props);

    this.loadData = this.loadData.bind(this);
    this.exportData = this.exportData.bind(this);
  }

  //Master function for loading data, split into 3 child functions
  loadData(e){

    const file = e.target.files[0];
    let effects, sounds = {};
    let meta = null;

    getMeta();

    function getMeta(){
      JSZip.loadAsync(file).then((zip)=>{
        try{
          zip.file('meta.json').async('string').then(function success(content){
            meta = JSON.parse(content);
            effects = meta.effects;
            sounds = meta.sounds;
            getAudio(zip);
          });
        } catch(e){
          alert("No meta.json file was found in the archive");
        }
      }).catch((error)=>{
        alert("I don't think that was the correct file");
      });
    }

    function getAudio(zip){
      console.log("GET AUDIO");
      let audio = {};
      let fileTotal = 0;
      let fileCount = 1;

      zip.forEach((path,entry)=>{
        if(!entry.dir){fileTotal++;}
      });

      console.log("FILE TOTAL: ", fileTotal);
      zip.forEach((path,entry)=>{
        if(!entry.dir){
          if(entry.name !== 'meta.json'){
            zip.file(path).async('blob').then(function success(content){
              fileCount++;
              Object.assign(audio,formatAudioObj(content, entry.name, audio));
              if(fileCount === fileTotal){finish(audio);}
            }, function error(e){
              alert(e);
            });
          }
        }
      });
    }

    const finish = (function finish(audio){
      const audioArr = objKeyArray(audio);
      const blob = window.URL || window.webkitURL;

      audioArr.forEach((s) =>{
        sounds[s].url = blob.createObjectURL(audio[s]);
        sounds[s].file = audio[s];
        sounds[s].playing = false;
      });
      console.log(sounds);
      this.props.load(effects, sounds);
    }).bind(this);
  }

  exportData(){
    const {sounds} = this.props;
    const str = JSON.stringify(this.props.state);
    const file = new File([str], 'meta.json', {type: "text/plain;charset=utf-8"});
    const soundArr = objKeysToArr(sounds)

    let zip = new JSZip();

    zip.file("meta.json", file);

    soundArr.forEach((sound)=>{
      zip.folder("sounds")
      .folder(sound.id)
      .file(sound.id + '.' + getFileExtension(sound.file.name).toLowerCase(), sound.file)
    });

    zip.generateAsync({type: "blob"}).then((content)=>{
      FileSaver.saveAs(content,'Audio.scrump');
    });
  }

  render(){
    return(
      <div className="controls-container dark">
        <ul className="mx-nav">
          <li className="title">
            <h4>SOUND MIXER</h4>
          </li>
          <li>
            <a onClick={(e) => simulateClick(this.loadInput)}>Load</a>
            <input type="file" style={{display: "none"}} onChange={this.loadData} ref={(ref) => {this.loadInput = ref}}></input>
          </li>
          <li>
            <a onClick={this.exportData}>Export</a>
          </li>
          <li>
            <a onClick={this.props.addEffect}>New Collection</a>
          </li>
          <hr/>
          <li>
            <a>How To Use</a>
          </li>
          <li>
            <a>About</a>
          </li>
        </ul>
      </div>
    )
  }
}

const mapStateToProps = (state) =>{
  return{
    sounds:state.sounds,
    state:state
  };
}
const mapDispatchToProps = (dispatch) =>{
  return{
    addEffect: () => {
      dispatch(addEffect());
    },
    load: (effects, sounds) =>{
      dispatch((dispatch)=>{
        dispatch(loadSounds(sounds));
        dispatch(loadEffects(effects));
      });
    }
  };
}

export default connect(mapStateToProps,mapDispatchToProps)(ControlsContainer);



