import React, {Component} from 'react';
import ReactHowler from 'react-howler';
import {volumeSliderStyle} from '../js/input';

import {EffectStyles} from '../actions/effectsConsts'

class Sound extends Component{
  constructor(props){
    super(props);

    this.state = {
      name: this.props.sound.name,
      editingName: false
    }

    this.nameInputKeypress = this.nameInputKeypress.bind(this);
    this.renderProgress = this.renderProgress.bind(this);
  }
  //Interval on component mount for rendering the progress bar.
  componentDidMount(){
    this.timer = setInterval(this.renderProgress,300);
    this.props.passUpPlayerRef(this.player);
  }
  //Clears the interval when the component unmounts.
  componentWillUnmount(){
    clearInterval(this.timer);
  }

  //Function for forcing updated of component to rerender the progress bar.
  renderProgress(){
    if(this.props.sound.playing){
      this.forceUpdate();
    }
  }

  //Checks for enter-key press when editing sound name
  nameInputKeypress(e){
    const {sound} = this.props;
    //Gets the keycode, if code is 13 (enter), it processes the name change
    const code = (e.keyCode ? e.keyCode : e.which);
    if(code === 13){
      const value = e.target.value.trim();
      if(value !== ""){sound.changeName(value);}
      this.setState({editingName: false});
    }
  }

  render(){
    const {sound,effect} = this.props;

    //Name of the sounds
    const name = () =>{
      if(this.state.editingName){
        return <input className="title" type="text" onKeyPress={this.nameInputKeypress} ref={(input) => {this.editNameInput = input}} onChange={(e)=>this.setState({name:e.target.value})} onBlur={()=> this.setState({editingName:false})} value={this.state.name}></input>;
      }
      return <p className="title" onClick={()=>this.setState({editingName:true})}>{sound.name}</p>;
    }
    //The progress bar styles
    const progress = () =>{
      //Determines if the player is loaded, if it is, the position of the progress bar is updated.
      if( typeof this.player  !== 'undefined' && this.player.howlerState() === 'loaded'){
        const x = (this.player.seek() / this.player.duration()) * 100;
        return {width: x+"%"}
      }
      return {};
    }
    //Media button function
    const mediaButton = () =>{
      let buttons = [];
      if(sound.playing){
        buttons.push(<a className="sound-pause" onClick={sound.pause}><i className="fas fa-pause"></i></a>);
      }else{
        buttons.push(<a className="sound-play" onClick={sound.play}><i className="fas fa-play"></i></a>);
      }

      buttons.push(<a className="sound-stop" onClick={()=>sound.stop(this.player)}><i className="fas fa-stop"></i></a>);
      return buttons;
    }
    //Calculates the volume level
    //Uses the level of the sound, the overall effect, and the sound's fade variable
    //To obtain a float between 0 - 1 that represents the final volume level
    const vol = (effect.volume/100) * (sound.volume/100) * sound.fade;
    //Updates the volume styles
    const volStyles = () =>{
      return volumeSliderStyle(sound.volume);
    }
    //Returns different volume icon and classes dependent on certain factors.
    const volIcon = () =>{
      //If the sound has been muted
      if(sound.mute){return "i-mx-volume-x muted";}
      //If the sound is muted by another sound's solo
      if(sound.soloMute){return "i-mx-volume-x";}
      //Volume icon based on volume level
      if(sound.volume > 50) {return "i-mx-volume-2";}
      else {return "i-mx-volume-1";}
    }
    //Generic class for setting an icon as active or inactive
    const activeIcon = (active) =>{
      return active ? "active" : "inactive";
    }

    ///////////////////////
    // Howler Variables //
    /////////////////////

    const loop = () =>{
      console.log(effect.style);
      if(sound.loop){
        switch(effect.style){
          case EffectStyles.DEFAULT:
            return true;
          case EffectStyles.RANDOM:
            return false;
          default:
            return true;
        }
      }
      return false;
    }

    return(
      <div className="sound-card">
        <span className="sound-progress" style={progress()}></span>
        <div className="sound-head d-flex align-items-center justify-content-between">
          {name()}
          <div className="head-options">
            <a className="sound-upload" onClick={sound.replace}><i className="fas fa-upload"></i></a>
            <a className="sound-close" onClick={sound.remove}><i className="fas fa-times"></i></a>
          </div>
        </div>
        <div className="sound-body">
          <div className="sound-controls d-flex align-items-center justify-content-between">
            <div className="media-buttons">
              {mediaButton()}
            </div>
            <div className="sound-volume d-flex align-items-center">
              <a className="volume-icon" onClick={sound.muteToggle}><i className={volIcon()}></i></a>
              <input className="volume-input" type="range" min="0" max="100" style={volStyles()} value={sound.volume} onChange={(e)=>sound.adjustVolume(e.target.value)}/>
            </div>
          </div>
          <hr/>
          <div className="sound-controls d-flex align-items-center justify-content-between">
            <a className="sound-loop font-size-10" onClick={sound.loopToggle}><i className={(activeIcon(sound.loop) + " i-mx-repeat")}></i></a>
            <a className="sound-solo font-size-8" onClick={sound.soloToggle}><span className={activeIcon(sound.solo)}>Solo</span></a>
            <a className="sound-fade font-size-8" onClick={sound.fadeToggle}><span className={activeIcon(sound.fading)}>Fade</span></a>
          </div>
        </div>
        <ReactHowler
          src={sound.url}
          format={[sound.ext]}
          playing={sound.playing}
          volume={vol}
          mute={sound.mute || effect.mute || sound.soloMute}
          loop={loop()}
          html5={false}
          preload={false}
          onLoad={sound.onLoad}
          onEnd={()=>sound.onFinish(this.player)}
          ref={(ref) => (this.player = ref)}/>
      </div>
    );
  }
}

export default Sound;



