import React, {Component} from 'react';

import SoundsContainer from '../containers/SoundsContainer';
import {EffectStyles} from '../actions/effectsConsts';

class Effect extends Component {
  constructor(props){
    super(props);

    this.state = {
      name: this.props.effect.name,
      menu: false,
      editingName: false
    }

    this.nameInputKeypress = this.nameInputKeypress.bind(this);
    this.changeName = this.changeName.bind(this);
    this.menuToggle = this.menuToggle.bind(this);
    this.inputBlur = this.inputBlur.bind(this);
    this.nameInputChange = this.nameInputChange.bind(this);
  }

  componentDidUpdate(prevProps, prevState, snapshot){
    if(prevState.editingName === false && this.state.editingName === true){
      this.setState({name:this.props.effect.name});
      this.editNameInput.focus();
    }
  }

  nameInputKeypress(e){
    const code = (e.keyCode ? e.keyCode : e.which);
    if(code === 13){
      this.changeName();
    }
  }
  menuToggle(){
    this.setState({menu: !this.state.menu})
    console.log(this.state.menu);
  }
  changeName(){
    const name = this.editNameInput.value.trim();
    if(name !== ""){
      this.props.changeVar(this.props.effect.id, 'name', name);
    }
    this.setState({editingName: false});
  }
  nameInputChange(e){
    this.setState({name:e.target.value});
  }
  inputBlur(){
    window.setTimeout(()=>{
      this.setState({editingName:false});
    },100)
  }

  render() {
    const {effect, deleteEffect, adjustVolume, createSounds, changeVar, loopAllSounds, fadeAllSounds} = this.props;
    const name = () =>{
      if(this.state.editingName){
        return (
          <div>
            <input type="text" onKeyPress={this.nameInputKeypress} ref={(input) => {this.editNameInput = input}} onChange={this.nameInputChange} value={this.state.name} onBlur={this.inputBlur}></input>
            <i className="fas fa-check" onClick={this.changeName} onFocus={()=>this.setState({editingName:true})}></i>
          </div>
        );
      }
      return <h4 className="title" onClick={()=>this.setState({editingName:true})}>{effect.name}</h4>;
    }
    const minimized = () =>{
      if(effect.minimized){
        return " minimized";
      }
      return "";
    }
    return (
      <div className={"effect-card dark" + minimized()}>
        <select className="effect-style-select" onChange={(e)=>{this.props.styleChange(e.target.value)}}>
          <option value={EffectStyles.DEFAULT} selected={effect.style === EffectStyles.DEFAULT}>Soundboard</option>
          <option value={EffectStyles.RANDOM} selected={effect.style === EffectStyles.RANDOM}>Random</option>
          <option value={EffectStyles.SEQUENTIAL} selected={effect.style === EffectStyles.SEQUENTIAL}>Sequential</option>
        </select>
        <a className="effect-close" onClick={()=>deleteEffect(effect.id)}><i className="fas fa-times"></i></a>
        <div className={this.state.menu ? "effect-menu open" : "effect-menu"}>
          <i className="menu-icon fas fa-ellipsis-v" onClick={this.menuToggle}></i>
          <div className="menu">
            <a onClick={loopAllSounds}>Loop All Sounds</a>
            <a onClick={fadeAllSounds}>Fade All Sounds</a>
          </div>
        </div>
        <div className="effect-head">
          {name()}
        </div>
        <SoundsContainer effectId={effect.id} adjustVolume={adjustVolume} createSounds={createSounds} changeEffectVar={changeVar}/>
        <div className="minimize">
          <hr></hr>
          <p className="arrow-down"><i className="fas fa-chevron-down" onClick={()=>this.props.changeVar(effect.id,"minimized",!effect.minimized)}></i></p>
          <p className="arrow-up"><i className="fas fa-chevron-up" onClick={()=>this.props.changeVar(effect.id,"minimized",!effect.minimized)}></i></p>
        </div>
      </div>);
  }
}

export default Effect;



