import UUID from 'uuid/v4'

export const LOAD_EFFECTS = "LOAD_EFFECTS";
export const ADD_EFFECT = "ADD_EFFECT";
export const REMOVE_EFFECT = "REMOVE_EFFECT";
export const ADD_SOUND_TO_EFFECT = "ADD_SOUND_TO_EFFECT";
export const MOVE_SOUND = "MOVE_SOUND-EFFECT";
export const REMOVE_SOUND_REFERENCE = "REMOVE_SOUND_REFERENCE";
export const ADD_SOUND_REFERENCE = "ADD_SOUND_REFERENCE";
export const CHANGE_EFFECT_VAR = "CHANGE_EFFECT_VAR";

export const EffectStyles = Object.freeze({
  DEFAULT: "default",
  RANDOM: "random",
  SEQUENTIAL: "sequential"
});

export function effectBuilder(name){
  return {
    id: UUID(),
    name: name,
    volume: 100,
    mute: false,
    style: EffectStyles.DEFAULT,
    randomLoop: false,
    sounds: [],
    minimized: false
  }
}



