import React, {Component} from 'react';
import {connect} from 'react-redux';

import Effect from '../components/effect';
import {addEffect, removeEffect, addSoundToEffect, changeEffectVar, loadEffects} from '../actions/effectsActions';
import {EffectStyles} from '../actions/effectsConsts';

import {createSound, loadSounds, changeSoundVar} from '../actions/soundsActions';
import {soundBuilder} from '../actions/soundsConsts';

import {objKeysToArr} from '../Helpers'

class EffectContainer extends Component{
  constructor(props){
    super(props);

    this.adjustVolume = this.adjustVolume.bind(this);
    this.loopAllSounds = this.loopAllSounds.bind(this);
    this.fadeAllSounds = this.fadeAllSounds.bind(this);
    this.styleChange = this.styleChange.bind(this);
  }

  adjustVolume(effectId, value){
    this.props.changeVar(effectId, 'volume', value);
  }

  loopAllSounds(id){
    this.props.loopAllSounds(this.props.effects[id].sounds);
  }

  fadeAllSounds(id){
    this.props.fadeAllSounds(this.props.effects[id].sounds);
  }

  styleChange(id,value){
    this.props.changeVar(id,'style',value);
  }
  //////////////////////
  // Debug Functions //
  ////////////////////

  render(){
    const effects = objKeysToArr(this.props.effects).map((effect, i) => {
      return (
        <div className="col-lg-4 col-md-5" key={effect.id}>
          <Effect
          key={effect.id}
          effect={effect}
          deleteEffect={this.props.deleteEffect}
          changeVar={this.props.changeVar}
          createSounds={this.props.createSounds}
          adjustVolume={this.adjustVolume}
          loopAllSounds={()=>this.loopAllSounds(effect.id)}
          fadeAllSounds={()=>this.fadeAllSounds(effect.id)}
          styleChange={(value)=>this.styleChange(effect.id,value)}
          />
        </div>
      );
    });
    return(
      <div>
        <div className="row">
          {effects}
          <div className="col-lg-4 col-md-5">
            <div className="effect-card dark effect-card-add" onClick={this.props.addEffect}>
              <p><i className="fas fa-plus mr-2"></i>Add A New Collection</p>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) =>{
  return{
    effects: state.effects,
    sounds: state.sounds,
    state: state
  }
}
const mapDispatchToProps = (dispatch) =>{
  return{
    addEffect: () => {
      dispatch(addEffect());
    },
    deleteEffect: (effectId) =>{
      dispatch(removeEffect(effectId));
    },
    createSounds:(effectId, fileArr)=>{
      dispatch(function(dispatch){
        fileArr.forEach((file)=>{
          const sound = soundBuilder(effectId, file.file, file.fileUrl);
          dispatch(createSound(sound));
          dispatch(addSoundToEffect(sound.id, effectId));
        });
      });
    },
    changeVar: (id, varName, value, extras)=>{
      dispatch(changeEffectVar(id,varName,value));
    },
    load: (effects, sounds) =>{
      dispatch((dispatch)=>{
        dispatch(loadSounds(sounds));
        dispatch(loadEffects(effects));
      });
    },
    loopAllSounds: (ids) =>{
      dispatch((dispatch)=>{
        ids.forEach((id)=>{
          dispatch(changeSoundVar(id,"loop",true));
        });
      });
    },
    fadeAllSounds: (ids) =>{
      dispatch((dispatch)=>{
        ids.forEach((id)=>{
          dispatch(changeSoundVar(id,"fading",true));
        });
      });
    }
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(EffectContainer);



